.header {
  padding-top: 30px;
  padding-bottom: 95px;
  background-image: $conic-red-to-blue;

  @include vp-1199 {
    padding-top: 34px;
    padding-bottom: 104px;
    background-image: $conic-red-to-blue-tablet;
  }

  @include vp-767 {
    padding-top: 14px;
    padding-bottom: 140px;
    background-image: $conic-red-to-blue-phone;
  }
}

.header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 88px;

  @include vp-1199 {
    margin-bottom: 76px;
    align-items: center;
  }

  @include vp-767 {
    margin-bottom: 70px;
  }
}

.header__logo-icon {
  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 69px;
    height: 24px;
  }
}

.header__link-box {
  max-width: 170px;
  align-self: flex-start;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.header__link {
  font-family: $ff-placeholder;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: $color-default-white;
  user-select: none;

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.5;
  }

  @include vp-1199 {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }
}
